  --accent: #f8d24e;
  --second-accent: #d6936d;
  --primary-color-btn: #0a8817;
  --disabled-btn: #ababab;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
