.primary-btn {
  text-align: center;
  text-align: center;
  color: var(--primary-text-color);
  background-color: var(--accent);
  border: none;
  border-radius: 10px;
  align-items: center;
  padding: 12px 34px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
  transition: color .3s cubic-bezier(.4, 0, .2, 1), visibility .3s, background-color .3s cubic-bezier(.4, 0, .2, 1), visibility .3s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.primary-btn:hover {
  background-color: var(--primary-color-btn);
}

.primary-btn:focus {
  color: var(--light-color);
}

.primary-btn:disabled {
  color: var(--primary-text-color);
  background-color: var(--disabled-btn);
}

.menu-svg-one {
  height: 20px;
  margin-left: 5px;
  transition: transform .25s linear;
}

.primary-btn:hover .menu-svg-one, .primary-btn:focus .menu-svg-one {
  transform: translateY(-40px);
}

.menu-svg-two {
  fill: currentColor;
  transition: transform .25s linear;
  position: absolute;
  top: 39%;
  right: 35px;
  transform: translateY(40px);
}

.primary-btn:hover .menu-svg-two, .primary-btn:focus .menu-svg-two {
  transform: translateY(0);
}

